import React from "react";

const SelectLeaveTypeModal = () => {
  return (
    <div className="modal fade selectModal" id="selectModal">
      <div className="modal-dialog modal-dialog-centered">
        <div className="modal-content rounded-0 border-0">
          <div className="modal-header border-0 pb-0">
            <h1 className="modal-title fs-5 fw-bold text-primary">Select</h1>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            />
          </div>
          <div className="modal-body">
            <div className="row px-md-1">
              <div className="col-md-6 px-md-2 mb-3">
                <a
                  href="#applyLeaveCanvas"
                  data-bs-toggle="offcanvas"
                  className="totalBx h-100"
                >
                  <figure>
                    <img src="images/totalleaves-icon.svg" alt="" width={50} />
                  </figure>
                  <h3 className="fw-bolder my-0">Apply Leave</h3>
                  <img
                    src="images/totalattendance.svg"
                    className="cornerLine"
                    alt=""
                    width={95}
                  />
                </a>
              </div>
              <div className="col-md-6 px-md-2 mb-3">
                <a
                  href="#applyTourCanvas"
                  data-bs-toggle="offcanvas"
                  className="totalBx h-100"
                >
                  <figure>
                    <img src="images/apply-tour.svg" alt="" width={50} />
                  </figure>
                  <h3 className="fw-bolder my-0">Apply Tour</h3>
                  <img
                    src="images/totalattendance.svg"
                    className="cornerLine"
                    alt=""
                    width={80}
                  />
                </a>
              </div>
              <div className="col-md-6 px-md-2 mb-3">
                <a
                  href="#attendanceCorrection"
                  data-bs-toggle="offcanvas"
                  className="totalBx h-100"
                >
                  <figure>
                    <img
                      src="images/attendance-correction.svg"
                      alt=""
                      width={50}
                    />
                  </figure>
                  <h3 className="fw-bolder my-0">Attendance Correction</h3>
                  <img
                    src="images/totalattendance.svg"
                    className="cornerLine"
                    alt=""
                    width={80}
                  />
                </a>
              </div>
              <div className="col-md-6 px-md-2 mb-3">
                <a
                  href="#viewModal"
                  data-bs-toggle="modal"
                  className="totalBx h-100"
                >
                  <figure>
                    <img src="images/attendance-logs.svg" alt="" width={50} />
                  </figure>
                  <h3 className="fw-bolder my-0">Attendance Logs</h3>
                  <img
                    src="images/totalattendance.svg"
                    className="cornerLine"
                    alt=""
                    width={80}
                  />
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SelectLeaveTypeModal;
