import React from "react";
import { weekDays } from "../../utils/Constants.config";
import { months } from "../../utils/Constants.config";

const AttendanceStatusCount = () => {
  let today = new Date();
  return (
    <>
      <div className="cardBox mb-3">
        <div className="d-flex flex-wrap align-items-center">
          <div className="d-flex align-items-center gap-2 currentDate">
            <figure className="mb-0">
              <img src="images/date-icon.svg" alt="" width={55} />
            </figure>
            <p className="m-0">
              <strong>
                Today,{" "}
                {`${today.getDate()} ${
                  months[today.getMonth()]
                } ${today.getFullYear()}`}
              </strong>
              {`${weekDays[today.getDay()]}`}
            </p>
          </div>
          <ul className="leavesList">
            <li>
              <div className="leavesTxt greenColor">
                <span>Present</span>
                <div className="d-flex align-items-center justify-content-between">
                  <strong>3 PR</strong>
                  <i className="bi bi-arrow-up-right-square-fill" />
                </div>
              </div>
            </li>
            <li>
              <div className="leavesTxt redColor">
                <span>Half Day</span>
                <div className="d-flex align-items-center justify-content-between">
                  <strong>0 HD</strong>
                  <i className="bi bi-arrow-up-right-square-fill" />
                </div>
              </div>
            </li>
            <li>
              <div className="leavesTxt redColor">
                <span>Absent</span>
                <div className="d-flex align-items-center justify-content-between">
                  <strong>1 AB</strong>
                  <i className="bi bi-arrow-up-right-square-fill" />
                </div>
              </div>
            </li>
            <li>
              <div className="leavesTxt phColor">
                <span>Public Holiday</span>
                <div className="d-flex align-items-center justify-content-between">
                  <strong>1 PH</strong>
                  <i className="bi bi-arrow-up-right-square-fill" />
                </div>
              </div>
            </li>
            <li>
              <div className="leavesTxt">
                <span>Weekly Off</span>
                <div className="d-flex align-items-center justify-content-between">
                  <strong>0 WO</strong>
                  <i className="bi bi-arrow-up-right-square-fill" />
                </div>
              </div>
            </li>
            <li>
              <div className="leavesTxt plColor">
                <span>Paid Leave</span>
                <div className="d-flex align-items-center justify-content-between">
                  <strong>0 PL</strong>
                  <i className="bi bi-arrow-up-right-square-fill" />
                </div>
              </div>
            </li>
            <li>
              <div className="leavesTxt yelloColor">
                <span>Leave Without Pay</span>
                <div className="d-flex align-items-center justify-content-between">
                  <strong>0 LW</strong>
                  <i className="bi bi-arrow-up-right-square-fill" />
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </>
  );
};

export default AttendanceStatusCount;
